:root {
  --fontFamily: Roboto, sans-serif;
  --accentBtnColor: #ff6b02;
  --accentTextColor: #ff6b08;
  --notificationTextColor: #ff001b;
  --primaryTextDark: #000;
  --primaryTextLight: #fff;
  --secondaryTextColor: #545454;
  --secondaryTextDetailsColor: #8c8c8c;
  --primaryLightThemeBgColor: #fff;
  --secondaryLightThemeBgColor: #f7f7f7;
  --headerGradientColor: rgba(0, 0, 0, .56);
  --secondDarkBgColor: #0f0f0f;
  --timingFunction: 250ms cubic-bezier(.4, 0, .2, 1);
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

body {
  font-family: var(--fontFamily);
  color: var(--primaryTextDark);
  background-color: var(--primaryLightThemeBgColor);
  font-size: 14px;
  font-weight: 400;
  line-height: 1.14;
}

img {
