.backdrop {
  width: 100%;
  height: 100%;
  z-index: 40;
  visibility: hidden;
  pointer-events: none;
  opacity: 0;
  transition: all var(--transition);
  background-color: rgba(0, 0, 0, .2);
  position: fixed;
  top: 0;
  left: 0;
  overflow: scroll;
}

.backdrop.active {
  visibility: visible;
  opacity: 1;
  pointer-events: all;
}

.backdrop .modal {
  transform: translateX(-50%)scale(.6);
}

@media screen and (min-width: 1280px) {
  .backdrop .modal {
    transform: translate(-50%, -50%)scale(.6);
  }
}

.modal1.active, .modal2.active, .modal3.active {
  visibility: visible;
  opacity: 1;
