.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.2);
    z-index: 40;
    visibility: hidden;
    pointer-events: none;
    opacity: 0;
    overflow: scroll;
    transition: all var(--transition);
}

.backdrop.active {
    visibility: visible;
    opacity: 1;
    pointer-events: all;
}

.backdrop .modal {
    transform: translateX(-50%) scale(0.6);

    @include desktop {
        transform: translate(-50%, -50%) scale(0.6);
    }
}

.modal1.active,
.modal2.active,
.modal3.active {
    visibility: visible;
    opacity: 1;
    pointer-events: initial;
}